import './Ghavanin.css'
import { Helmet } from 'react-helmet';
import { useTranslation } from 'react-i18next';

// Icons


import ghavaninpolicyicon from './Icons/ghavaninpolicyicon.svg'
import Soalat from './Soalat';
const Ghavanin = () => {
  const { t } = useTranslation();

    return ( 
        <div>
            <Helmet>
                <title>  -   </title>
            </Helmet>
            <div className="header-ghavanin">
                <h1><img src={ghavaninpolicyicon} alt="icon ghavanin" />{t('  ')}</h1>
            </div>
            <div className="ghavanin-content">
                <div className="ghavanin-content-back">
                <Soalat Soalheader={t(' ')} Soalcontent={t('                                          .')}></Soalat>
                <Soalat Soalheader={t('      ')} Soalcontent={t('                             .')}></Soalat>
                <Soalat Soalheader={t('     ')} Soalcontent={t('                                  .')}></Soalat>
                <Soalat Soalheader={t('     ')} Soalcontent={t('               ')} Soalcontent2={t('1-               .')} Soalcontent3={t('2-            .')}></Soalat>
                </div>
            </div>    
        </div>
     );
}
 
export default Ghavanin;